import React from 'react';
import { Button, Popconfirm, Icon } from 'antd';
import { connect } from 'react-redux';
import * as creators from '../store/creators';
import { withRouter, Link } from 'react-router-dom';

function Oper(props) {
  const fontSmall = { fontSize: "12px", marginLeft: "5px" };
  return (
    <div>
      <Link
        to={{
          pathname: "/tokenRole/add",
          state: { editRecord: props.record }
        }}
      >
        <Button
          style={fontSmall}
          type="primary"
          size="small"
          ghost
        ></Button>
      </Link>
      <Link
        to={{
          pathname: "/tokenRole/config",
          state: { record: props.record }
        }}
      >
        <Button
          style={fontSmall}
          type="primary"
          size="small"
          ghost
        ></Button>
      </Link>
      <Button
        style={fontSmall}
        type="primary"
        size="small"
        ghost
        onClick={() => {
          const data = "roleName=" + props.record.name
          props.updatePower({ props, data })
        }}
      ></Button>
      <Popconfirm
        placement="left"
        title="?"
        onConfirm={() => {
          const data = "id=" + props.record.id
          props.delete({ props, data })
        }}
        okText=""
        icon={<Icon type="question-circle" />}
        cancelText="">
        <Button style={fontSmall} type="danger" size="small" ghost></Button>
      </Popconfirm>

    </div >
  )
}

const mapDispatch = dispatch => ({
  delete: req => {
    const action = creators.deleteAction(req);
    dispatch(action);
  },
  updatePower: req => {
    const action = creators.updatePowerAction(req);
    dispatch(action);
  },
})

export default withRouter(connect(null, mapDispatch)(Oper));
